header {
  position: fixed;
  top: 0px;
  background-color: white;
  width: 100vw;
  border-bottom: 100px solid #fff;
  border-width: 0;
  padding-bottom: 0%;
  z-index: 1;
}
.nav-links {
  flex: 2;
}
.nav-links > ul {
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav-link {
  position: relative;
}
.nav-link > a {
  font: '--i: .6s';
  line-height: 3rem;
  color: grey;
  padding: 0 0.8rem;
  letter-spacing: 1px;
  font-size: 0.95rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: 0.5s;
}
.nav-link:hover > a {
  transform: scale(1.1);
  text-decoration: none;
  color: #45a049;
}

@keyframes animation {
  from {
    opacity: 0;
    transform: translateY(15px);
  }
  to {
    opacity: 1;
    transform: translateY(0px);
  }
}

@media (max-width: 300px) {
  .navbar-nav {
    
    flex: initial;
    width: 100%;
  }

  .nav-link {
    width: 100%;
    opacity: 0;
    transform: translateY(15px);
  }
}

