.flex-center {
  width: 100%;
  min-height: 50vh;
  margin-top: 20px;
  background: rgba(0, 0, 0, 0.8);
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
}
.icon-3d {
  padding: 10px;
  -webkit-animation: icon3d 200ms 10;
  animation: icon3d 200ms 10;
  color: #fff;
}
.icon-3d:hover {
  -webkit-animation: icon3d 200ms infinite;
  animation: icon3d 200ms infinite;
}
@keyframes icon3d {
  0% {
    text-shadow: 5px 4px rgba(25, 164, 222, 1), -5px -6px rgba(255, 99, 71, 1);
  }
  25% {
    text-shadow: -5px -6px rgba(25, 164, 222, 1), 5px 4px rgba(255, 99, 71, 1);
  }
  50% {
    text-shadow: 5px -4px rgba(25, 164, 222, 1), -8px 4px rgba(255, 99, 71, 1);
  }
  75% {
    text-shadow: -8px -4px rgba(25, 164, 222, 1), -5px -4px rgba(255, 99, 71, 1);
  }
  100% {
    text-shadow: -5px 0 rgba(25, 164, 222, 1), 5px -4px rgba(255, 99, 71, 1);
  }
}

