.btn {
  background-color: rgba(10, 5, 5, 0.836);
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 1.5em;
  text-decoration: none;
  margin-top: 2%;
  width: 45%;
  transition: 0.2s ease-in-out;
}
.btn:hover {
  background-color: black;
  color: white;
  width: 46%;
