.domains-card {
  width: 350px;
  position: relative;
  box-shadow: 0 2px 7px #dfdfdf;
  margin: 50px auto;
  background: #fafafa;
}
.domains-card:hover {
  background: rgb(0, 0, 0, 0.3);
  color: white;
}
.domains-card:hover p {
  color: white;
}
.domains-card:hover h4 a {
  font-weight: 700;
}
.domains-im {
