}
.domains-details {
  padding: 30px;
}
.domains-details h4 a {
  font-weight: 500;
  display: block;
  margin-bottom: 18px;
  text-transform: uppercase;
  color: #363636;
  text-decoration: none;
  transition: 0.3s;
}
.domains-details p {
  font-size: 15px;
